.loader {
  position: relative;
}

.loader:after {
  content: "";
  width: 32px;
  height: 32px;
  transform-origin: -16px -32px;
  background: pink;
  border-radius: 4px;
  animation: rotate 1s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes rotate {
  0%, 100% {
    transform: rotate(-45deg)translate(-50%, -50%);
  }

  50% {
    transform: rotate(-245deg)translate(-50%, -50%);
  }
}

